<template>
  <div id="main-app" class="transparent">
    <div class="row">
      <div class="col-12">
        <h4>{{multilanguage.findhotel[language]}}</h4>
        <br />
      </div>
    </div>
    <form v-on:submit="buscar" style="width:100%" class="needs-validation" novalidate>
      <div class="row">
        <template v-if="hotelshortdestinations">
          <div v-if="showdestination" class="col-sm-12 col-md-6 col-lg-4">
            <div v-if="portfolio" class="form-group">
              <label
                for="destino"
                data-error="wrong"
                data-success="right"
              >{{multilanguage.where[language]}} *</label>
              <select
                class="form-control"
                id="destino"
                v-model="destobject"
                style="cursor: pointer"
              >
                <template v-for="pais in portfolio.countries">
                  <option :key="pais.resourceId" :value="pais">{{ pais.name.display }}</option>
                  <template v-for="prov in pais.states">
                    <option
                      v-if="prov.name"
                      :value="prov"
                      :key="prov.resourceId"
                    >&emsp;{{ prov.name.display }}</option>
                    <template v-for="loc in prov.cities">
                      <option
                        v-if="loc.name"
                        :key="loc.resourceId"
                        :value="loc"
                        selected="true"
                      >&emsp;&emsp;{{ loc.name.display }}</option>
                    </template>
                  </template>
                </template>
              </select>
              <div class="invalid-feedback">
                <span id="destinoaux-msg"></span>
              </div>
            </div>
            <div v-else class="d-flex justify-content-center mt-4">
              <div class="spinner-border text-primary" role="status">
                <span class="sr-only">Loading...</span>
              </div>
            </div>
          </div>
        </template>
        <template v-else>
          <div v-if="showdestination" class="col-sm-12 col-md-6 col-lg-4">
            <div class="form-group">
              <label class="active" for="destino" data-error="wrong" data-success="right">
                {{multilanguage.destination[language]}}
                *
              </label>
              <input
                type="text"
                id="destino"
                class="bg-white validate form-control"
                readonly
                v-model="destobject.name.display"
                v-on:keyup="elegirdestino('destino')"
                v-on:focus="elegirdestino('destino')"
                style="cursor: pointer"
              />
              <div class="invalid-feedback">
                <span id="destino-msg"></span>
              </div>
            </div>
          </div>
        </template>
        <div class="col-sm-12 col-md-6 col-lg-3">
          <div class="form-group" style="cursor: pointer">
            <label for="checkindates" class="active">{{multilanguage.when[language]}} *</label>
            <v-date-picker
              class="validate invalid"
              aria-invalid="true"
              mode="range"
              is-double-paned
              v-model="selectedValue"
              :is-linked="true"
              :format="formats"
              :input-props="fechaentradaclass"
            ></v-date-picker>
            <input id="checkindates" class="validator" style="display:none;" />
            <div class="invalid-feedback">
              <span id="checkindates-msg"></span>
            </div>
          </div>
        </div>
        <div class="col-sm-12 col-md-6 col-lg-3">
          <div class="form-group" v-on:click="elegirocupacion">
            <label
              class="active"
              data-error="wrong"
              data-success="right"
            >{{multilanguage.who[language]}} *</label>
            <div class="occupation-container">
              <div class="occupation" style="cursor: pointer">
                <!--  <b>{{multilanguage.room[language]}} {{index +1}}:</b>-->
                <span v-for="(oc,index) in ocupaciones" class="pr" :key="index">
                  <i v-for="(n,indexn) in parseInt(oc.adult )" class="fas fa-male fa-2x" :key="indexn"></i>
                  <i v-for="(x,indexx) in parseInt(oc.children)" class="fas fa-child teal-text fa-lg" :key="indexx"></i>
                </span>
                <!--   {{totaladults}} x <i class="fas fa-male fa-2x"></i>  
                {{totalchild}} x <i class="fas fa-child  teal-text fa-lg"></i>-->
              </div>
            </div>
          </div>
        </div>
        <div class="col-sm-12 col-md-6 col-lg-2">
          <div class="form-group">
            <div class="button-container">
              <button
                type="button"
                class="btn btn-primary"
                v-on:click="buscar"
              >{{multilanguage.search[language]}}</button>
            </div>
          </div>
        </div>
      </div>
    </form>
    <!-- Modal Structure -->
    <div class="modal" id="modal1" tabindex="-1" role="dialog">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">{{multilanguage.choosedest[language]}}</h5>
            <button type="button" class="close" @click="closemodal()" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div>
              <div>
                <div class="form-group mt-3">
                  <label
                    for="busqueda"
                    data-error="wrong"
                    data-success="right"
                  >{{multilanguage.searchtext[language]}}</label>
                  <input
                    id="busqueda"
                    type="text"
                    class="validate form-control"
                    v-model="busqueda"
                    v-on:keyup="buscardestino"
                  />
                </div>
                <div class="destinocontent">
                  <div v-for="(c,index2) in coincidencias.matches" class="mb-2" v-bind:key="index2">
                    <h5 class="text-primary">
                      <a
                        v-on:click="seleccionardestino(c.name,c.resourceId)"
                        :data-key="c.resourceId"
                        :data-name="c.name"
                        style="cursor: pointer"
                      >{{ c.name }}</a>
                    </h5>
                    <span>{{ c.description }}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
              @click="closemodal()"
            >Close</button>
          </div>
        </div>
      </div>
    </div>
    <div id="modal2" class="modal" role="dialog">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <h4>{{multilanguage.whochange[language]}}</h4>
          <div class="row" v-for="(o, index) in ocupaciones" :key="index">
            <div class="col-sm-12 col-md-2 col-lg-2">
              <div class="input-field">
                <label class="active">{{multilanguage.room[language]}} {{index + 1}}</label>
              </div>
            </div>
            <div class="col-sm-12 col-md-2 col-lg-2">
              <div class="input-field">
                <input
                  type="number"
                  min="0"
                  :id="'adult' + index"
                  class="validate"
                  v-model="o.adult"
                  style="cursor: pointer"
                />
                <label
                  class="active"
                  :for="'adult' + index"
                  data-error="wrong"
                  data-success="right"
                >{{multilanguage.adult[language]}}</label>
              </div>
            </div>
            <div class="col-sm-12 col-md-2 col-lg-2">
              <div class="input-field">
                <input
                  type="number"
                  min="0"
                  id="'children' + index"
                  class="validate"
                  v-model="o.children"
                  style="cursor: pointer"
                />
                <label
                  class="active"
                  :for="'children' + index"
                  data-error="wrong"
                  data-success="right"
                >{{multilanguage.children[language]}}</label>
              </div>
            </div>
            <div class="col-sm-12 col-md-2 col-lg-2">
              <div v-if="o.children && parseInt(o.children.valueOf()) !== 0">
                <div
                  v-for="(n, index2) in parseInt(o.children.valueOf())"
                  class="input-field"
                  :key="index2"
                >
                  <label
                    class="active"
                    :for="'age' + index + index2"
                    data-error="wrong"
                    data-success="right"
                  >{{multilanguage.childages[language]}}</label>
                  <input
                    type="number"
                    min="0"
                    max="18"
                    :id="'age' + index + index2"
                    class="validate"
                    v-model="o.edades[index2]"
                    required
                    style="cursor: pointer"
                  />
                </div>
              </div>
            </div>
            <div class="col-sm-12 col-md-2 col-lg-2">
              <div class="input-field">
                <a
                  class="btn-floating btn-small waves-effect waves-light red"
                  v-on:click="borrarocupacion(index)"
                >
                  <i class="material-icons">delete</i>
                </a>
              </div>
            </div>
          </div>

          <a
            class="btn-floating btn-small waves-effect waves-light green left"
            v-on:click="anadirocupacion"
          >
            <i class="material-icons">add</i>
          </a>
        </div>
        <div class="modal-footer">
          <a
            class="modal-close waves-effect waves-green btn"
            @click="calculatetotal"
          >{{multilanguage.ok[language]}}</a>
        </div>
      </div>
    </div>
    <div
      class="modal fade"
      id="miModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="myModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h4 class="modal-title">{{multilanguage.whochange[language]}}</h4>
          </div>
          <div class="modal-body">
            <div class="row" v-for="(o, index) in ocupaciones" :key="index">
              <div class="col-sm-12 col-md-2 col-lg-2">
                <div>
                  <label class="active">{{multilanguage.room[language]}} {{index + 1}}</label>
                </div>
              </div>
              <div class="col-sm-12 col-md-2 col-lg-2">
                <div class="form-group">
                  <label
                    class="active"
                    :for="'adult' + index"
                    data-error="wrong"
                    data-success="right"
                  >{{multilanguage.adult[language]}}</label>
                  <input
                    type="number"
                    min="0"
                    :id="'adult' + index"
                    class="validate form-control"
                    v-model="o.adult"
                    style="cursor: pointer"
                  />
                </div>
              </div>
              <div class="col-sm-12 col-md-2 col-lg-2">
                <div class="input-field">
                  <label
                    class="active"
                    :for="'children' + index"
                    data-error="wrong"
                    data-success="right"
                  >{{multilanguage.children[language]}}</label>
                  <input
                    type="number"
                    min="0"
                    id="'children' + index"
                    class="validate form-control"
                    v-model="o.children"
                    style="cursor: pointer"
                  />
                </div>
              </div>
              <div class="col-sm-12 col-md-2 col-lg-2">
                <div v-if="o.children && parseInt(o.children.valueOf()) !== 0">
                  <div
                    v-for="(n, index2) in parseInt(o.children.valueOf())"
                    class="input-field"
                    :key="index2"
                  >
                    <label
                      class="active"
                      :for="'age' + index + index2"
                      data-error="wrong"
                      data-success="right"
                    >{{multilanguage.childages[language]}}</label>
                    <input
                      type="number"
                      min="0"
                      max="18"
                      :id="'age' + index + index2"
                      class="validate form-control"
                      v-model="o.edades[index2]"
                      required
                      style="cursor: pointer"
                    />
                  </div>
                </div>
              </div>
              <div class="col-sm-12 col-md-2 col-lg-2">
                <div class="input-field">
                  <a class="btn btn-transparent text-danger" v-on:click="borrarocupacion(index)">
                    <i class="material-icons">delete</i>
                  </a>
                </div>
              </div>
            </div>

            <a class="btn btn-transparent left text-success" v-on:click="anadirocupacion">
              <i class="material-icons">add</i>
            </a>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="modal-close btn btn-success"
              data-dismiss="modal"
              @click="calculatetotal"
            >{{multilanguage.ok[language]}}</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

var globallanguage = "es";
if (globaldata.language != null && globaldata.language != "") {
  globallanguage = globaldata.language;
}
var destview = true;
var globaldestination;

if (globaldata.whitebrandhotel !== null && globaldata.whitebrandhotel !== "") {
  globaldestination = globaldata.whitebrandhotel;
  destview = false;
}

const langfile = require("../../assets/lang/content.json");

var instance;
var opciones;

var modal1;
var modal2;

var highstart = new Date("2019", "04", "13");
var highend = new Date("2019", "04", "17");
var today = new Date();
var tomorrow = new Date();
tomorrow.setDate(tomorrow.getDate() + 1);

if (today < highstart) {
  console.log("entra");

  today = highstart;
  tomorrow = highend;

  console.log("tomorrow" + tomorrow);
  console.log("today" + today);
}

export default {
  name: "Formulario",
  data: function() {
    return {
      hotelshortdestinations:
        globaldata.hotelshortdestinations === true ? true : false,
      showmodal: false,
      showdestination: destview,
      selectedValue: {
        start: this.getInitialDate("entrada"),
        end: this.getInitialDate("salida")
      },
      destino: "",
      destobject: {
        resourceId: "",
        name: {
          display: ""
        }
      },
      iddestino: null,
      entrada: this.formatDate(today),
      salida: this.formatDate(tomorrow),
      ocupacion: "1x2",
      portfolio: null,
      busqueda: null,
      category: null,
      address: null,
      coincidencias: {
        matches: []
      },
      ocupaciones: [
        {
          adult: 2,
          children: 0,
          edades: []
        }
      ],
      totaladults: 2,
      totalchild: 0,
      language: globaldata.language,
      multilanguage: langfile.multilanguage,
      formats: {
        title: "MMMM YYYY",
        weekdays: "W",
        navMonths: "MMM",
        input: ["YYYY/MM/DD"], // Only for `v-date-picker`
        dayPopover: "L", // Only for `v-date-picker`
        data: ["YYYY/MM/DD"] // For attribute dates
      },
      fechaentradaclass: {
        class: "form-control "
      }
    };
  },
  methods: {
    elegirdestino: function(selectype) {
      this.$data.selectiontype = selectype;
      $(function() {
        $("#modal1").show();
        $("#busqueda").focus();
      });
    },
    closemodal: function(e) {
      var d = this.$data;
      d.busqueda = null;
      d.coincidencias = { matches: [] };

      $(function() {
        $("#modal1").toggle();
      });
    },
    elegirocupacion: function(e) {
      // modal2.open()
      $("#miModal").modal("show");
      this.$data.totaladults = 0;
      this.$data.totalchild = 0;
    },
    borrarocupacion: function(index) {
      if (this.$data.ocupaciones.length > 1)
        this.$data.ocupaciones.splice(index, 1);
    },
    anadirocupacion: function(e) {
      this.$data.ocupaciones.push({ adult: 2, children: 0, edades: [] });

      //  M.updateTextFields()
    },
    calculatetotal: function(e) {
      this.$data.totaladults = 0;
      this.$data.totalchild = 0;
      for (var i = 0; i < this.$data.ocupaciones.length; i++) {
        console.log("oc");
        console.log(this.$data.ocupaciones[i].adult);
        console.log(this.$data.ocupaciones[i].children);
        this.$data.totaladults =
          this.$data.totaladults + parseInt(this.$data.ocupaciones[i].adult);
        this.$data.totalchild =
          this.$data.totalchild + parseInt(this.$data.ocupaciones[i].children);
      }
    },
    buscardestino: function(e) {
      var especial = false;
      especial |= e.keyCode === 37;
      especial |= e.keyCode === 38;
      especial |= e.keyCode === 39;
      especial |= e.keyCode === 40;

      if (!especial) {
        var coins = this.$data.coincidencias;

        axios({
          method: "get",
          url: globaldata.baseurl + "/commons/search",
          params: {
            language: globaldata.language,
            query: this.$data.busqueda
          }
        })
          .then(function(response) {
            coins.matches = response.data.matches;
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    seleccionardestino: function(name, resourceId) {
      this.$data.destobject.name[this.$data.language] = name;
      this.$data.destobject.name.display = name;
      this.$data.destobject.resourceId = resourceId;

      this.closemodal();
    },
    buscardestinox: function(e) {
      var especial = false;
      especial |= e.keyCode === 37;
      especial |= e.keyCode === 38;
      especial |= e.keyCode === 39;
      especial |= e.keyCode === 40;

      if (!especial) {
        axios({
          method: "get",
          url: globaldata.baseurl + "/commons/search",
          params: {
            language: globaldata.language,
            query: this.$data.destobject.name.display
          }
        })
          .then(function(response) {
            var opts = {};
            opciones = response.data.matches;
            for (var m in opciones) {
              opts[opciones[m].name] = null;
            }

            instance.updateData(opts);
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    setValidationError(field, msgField, msg) {
      var d = this.$data;

      if (field === "checkindates") {
        d.fechaentradaclass.class = "form-control  is-invalid";
      }
      var element = document.getElementById(field);
      element.classList.add("is-invalid");
      element.classList.add("form-control");
      element.focus();

      if (msgField !== null && msgField !== " ") {
        var elementmsg = document.getElementById(msgField);

        elementmsg.innerHTML = msg;
      }
    },
    buscar: function(e) {
      e.preventDefault();
      if (!destview) {
        this.$data.destobject.resourceId = globaldestination;
        this.$data.destobject.name.display = "";
      }
      if (this.checkForm()) {
        if (
          globaldata.whitebrandhotel !== null &&
          globaldata.whitebrandhotel !== ""
        ) {
          this.$router.push({
            name: "hotel_rates",
            query: {
              hotelkey: globaldata.whitebrandhotel,
              hotelname: this.$data.destobject.name.display,
              category: this.$data.category,
              address: this.$data.address,
              entrada: this.formatDate(this.$data.selectedValue.start),
              salida: this.formatDate(this.$data.selectedValue.end),
              ocupacion: this.$data.ocupacion
            }
          });
        } else {
          this.$router.push({
            name: "hotel_dispo",
            query: {
              iddestino: this.$data.destobject.resourceId,
              destino: this.$data.destobject.name.display,
              entrada: this.formatDate(this.$data.selectedValue.start),
              salida: this.formatDate(this.$data.selectedValue.end),
              ocupacion: this.$data.ocupacion
            }
          });
        }
      }
    },
    buscarcalendario: function(e) {
      this.$router.push({
        name: "hotel_calendario",
        query: {
          destino: this.$data.destobject.resourceId,
          destinoname: this.$data.destobject.name.display,
          entrada: this.formatDate(this.$data.selectedValue.start),
          salida: this.formatDate(this.$data.selectedValue.end),
          ocupacion: this.$data.ocupacion
        }
      });
    },
    formatDate: function(date) {
      var d = new Date(date);
      var month = "" + (d.getMonth() + 1);
      var day = "" + d.getDate();
      var year = d.getFullYear();

      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;

      return [year, month, day].join("-");
    },
    formatStringtoOcupaciones: function(param) {
      var d = this.$data;
      d.totaladults = 0;
      d.totalchild = 0;
      d.ocupaciones = [];
      var ocupancies = param.split(",");
      console.log(ocupancies);

      for (var i = 0; i < ocupancies.length; i++) {
        console.log(ocupancies[i]);
        var ocup = ocupancies[i].split("x");
        var pax = ocup[1];
        console.log(pax);
        var paxOcup = ocup[1].split("-");
        var totalpax = paxOcup[0];
        var child = 0;
        var edades = [];
        if (paxOcup.length > 1) {
          child = paxOcup.length - 1;

          for (var j = 1; j < paxOcup.length; j++) {
            edades.push(paxOcup[j]);
          }
        }

        var adult = totalpax - child;

        console.log(edades);
        console.log("adult" + adult);
        console.log("child" + child);
        var ocuppAux = {};
        ocuppAux.adult = adult;
        ocuppAux.children = child;
        ocuppAux.edades = edades;
        d.ocupaciones.push(ocuppAux);
        d.totaladults = d.totaladults + adult;
        d.totalchild = d.totalchild + child;
      }
    },
    checkForm: function(e) {
      var existsErrors = false;
      this.cleanErrors();
      if (!destview) {
        if (this.ocupacion === null || this.ocupacion === "") {
          existsErrors = true;
          this.setValidationError(
            "destino",
            "destinoaux-msg",
            this.multilanguage.errordestinationempty[this.language]
          );
        }
      }

      if (this.selectedValue === null || this.selectedValue === "") {
        this.setValidationError(
          "checkindates",
          "checkindates-msg",
          this.multilanguage.errordatesempty[this.language]
        );
        existsErrors = true;
      } else {
        if (
          this.selectedValue.start === null ||
          this.selectedValue.start === ""
        ) {
          this.setValidationError(
            "checkindates",
            "checkindates-msg",
            this.multilanguage.errorcheckinempty[this.language]
          );
          existsErrors = true;
        }
        if (this.selectedValue.end === null || this.end === "") {
          this.setValidationError(
            "checkindates",
            "checkindates-msg",
            this.multilanguage.errorcheckoutempty[this.language]
          );
          existsErrors = true;
        }

        var fsalida = new Date(this.selectedValue.end);
        var fentrada = new Date(this.selectedValue.start);
        var today = new Date();
        fsalida.setHours(0, 0, 0, 0);
        fentrada.setHours(0, 0, 0, 0);
        today.setHours(0, 0, 0, 0);

        if (fentrada.getTime() >= fsalida.getTime()) {
          this.setValidationError(
            "checkindates",
            "checkindates-msg",
            this.multilanguage.errorcheckincheckout[this.language]
          );
          existsErrors = true;
        }
        if (fentrada.getTime() < today.getTime()) {
          this.setValidationError(
            "checkindates",
            "checkindates-msg",
            this.multilanguage.errorcheckintoday[this.language]
          );
          existsErrors = true;
        }
      }
      if (
        !this.destobject.resourceId ||
        this.destobject.resourceId === null ||
        this.destobject.resourceId === ""
      ) {
        this.setValidationError(
          "destino",
          "destinoaux-msg",
          this.multilanguage.errordestinationempty[this.language]
        );
        existsErrors = true;
      }

      if (existsErrors) {
        return false;
      }
      return true;
    },
    cleanErrors: function() {
      var d = this.$data;
      var element;
      if (destview) {
        element = document.getElementById("destino");
        element.setAttribute("class", "form-control bg-white ");
      }

      element = document.getElementById("checkindates");
      d.fechaentradaclass.class = "form-control";
      element.setAttribute("class", "form-control ");
    },
    getInitialDate: function(dt) {
      let maindata = JSON.parse(localStorage.getItem("qtse_maindatastorage"));
      if (dt === "entrada") {
        if (this.$route.query.entrada) {
          return new Date(this.$route.query.entrada);
        } else {
          if (maindata && maindata.startdate) {
            return new Date(maindata.startdate);
          } else {
            return today;
          }
        }
      }
      if (dt === "salida") {
        if (this.$route.query.salida) {
          return new Date(this.$route.query.salida);
        } else {
          if (maindata && maindata.enddate) {
            return new Date(maindata.enddate);
          } else {
            return tomorrow;
          }
        }
      }
    }
  },
  mounted() {
    var d = this.$data;
    var _this = this;

    axios({
      method: "get",
      url: globaldata.baseurl + "/commons/portfolio"
    })
      .then(function(response) {
        d.portfolio = response.data;

        for (var i = 0; i < d.portfolio.countries.length; i++) {
          d.portfolio.countries[i].name.display =
            d.portfolio.countries[i].name.es;

          for (var j = 0; j < d.portfolio.countries[i].states.length; j++) {
            if (d.portfolio.countries[i].states[j].name) {
              d.portfolio.countries[i].states[j].name.display =
                d.portfolio.countries[i].states[j].name.es;
            }
            for (
              var k = 0;
              k < d.portfolio.countries[i].states[j].cities.length;
              k++
            ) {
              d.portfolio.countries[i].states[j].cities[k].name.display =
                d.portfolio.countries[i].states[j].cities[k].name.es;
            }
          }
        }

        d.destobject = d.portfolio.countries[0].states[0];

        if (_this.$route.query.destino && _this.$route.query.iddestino) {
          d.destobject.resourceId = _this.$route.query.iddestino;
          d.destobject.name.display = _this.$route.query.destino;
          d.destobject.name[d.language] = _this.$route.query.destino;
        }

        d.selectedValue.start = _this.getInitialDate("entrada");
        d.selectedValue.end = _this.getInitialDate("salida");

        if (_this.$route.query.ocupacion) {
          d.ocupacion = _this.$route.query.ocupacion;

          _this.formatStringtoOcupaciones(_this.$route.query.ocupacion);
        }
      })
      .catch(function(error) {
        d.portfolio = "1"; //Stops spinner
        console.log(error);
      });
  }
};
</script>

