<template>
  <div id="main-app" class="transparent">
    <link
      rel="stylesheet"
      href="https://use.fontawesome.com/releases/v5.3.1/css/all.css"
      integrity="sha384-mzrmE5qonljUremFsqc01SB46JvROS7bZs3IO2EmfFsd15uHvIt+Y8vEf7N7fWAU"
      crossorigin="anonymous"
    />
    <div id="steps" class="row">
      <template v-if="showwhite">
        <div class="col-3">
          <button class="btn btn-success rounded-circle" @click="goback('formulario')">1</button>
          <a class="black-text" style="cursor: pointer;" @click="goback('formulario')">
            <span class="step-text center">{{multilanguage.selectdestinationdate[language]}}</span>
          </a>
        </div>
        <div class="col-3">
          <button class="btn btn-primary rounded-circle" style="cursor: default">2</button>
          <span class="step-text center">{{multilanguage.selectroom[language]}}</span>
        </div>
        <div class="col-3">
          <button class="btn btn-dark rounded-circle" disabled style="cursor: default">3</button>
          <span class="step-text center">{{multilanguage.fillcheckbook[language]}}</span>
        </div>
      </template>
      <template v-else>
        <div class="col-3">
          <button class="btn btn-success rounded-circle" @click="goback('formulario')">1</button>
          <a class="black-text" style="cursor: pointer;" @click="goback('formulario')">
            <span class="step-text center">{{multilanguage.selectdestinationdate[language]}}</span>
          </a>
        </div>
        <div class="col-3">
          <button class="btn btn-success rounded-circle" @click="goback('dispo')">2</button>
          <a class="black-text" style="cursor: pointer;" @click="goback('dispo')">
            <span class="step-text center">{{multilanguage.selecthotel[language]}}</span>
          </a>
        </div>
        <div class="col-3">
          <button class="btn btn-primary rounded-circle" style="cursor: default">3</button>
          <span class="step-text center">{{multilanguage.selectroom[language]}}</span>
        </div>
        <div class="col-3">
          <button class="btn btn-dark rounded-circle" disabled style="cursor: default">4</button>
          <span class="step-text center">{{multilanguage.fillcheckbook[language]}}</span>
        </div>
      </template>
    </div>
    <div class="row">
      <div class="col-12 mt-5">
        <div class="hoteltitle">{{title}}</div>
        <div v-if="stars >0" class="category">
          <span v-for="(cat,indexcat) in  parseInt(stars)" :key="'a'+indexcat">
            <i class="material-icons tiny">star</i>
          </span>
        </div>
        <div v-else class="category">
          <span v-for="(cat,indexcat2) in  parseInt(keys)" :key="'b'+indexcat2">
            <i class="icon-rotate material-icons tiny">vpn_key</i>
          </span>
        </div>
        <div class="hoteladdress">{{address}}</div>
      </div>
    </div>
    <div class="row">
      <div class="col-sm-12 col-md-6 col-lg-6">
        <Ficha :rate="hotelid"></Ficha>
      </div>
      <div class="col-sm-12 col-md-6 col-lg-6">
        <div class="calendar-title">{{multilanguage.changedates[language]}}</div>
        <div v-if="loaderdate " class="center mt-5 pt-5">
          <div class="spinner-border text-primary"></div>
        </div>
        <div v-else>
          <div v-if="charged">
            <v-date-picker
              width="100%"
              mode="range"
              is-double-paned
              v-model="selectedValue"
              :format="formats"
              :to-page="to_page"
              :from-page.sync="from_page"
              :is-linked="true"
              :is-expanded="true"
              :available-dates="availabledates"
              :disabled-dates="disableddates"
              :is-inline="true"
              :disabled-attribute="disableattr"
              :drag-attribute="dragattr"
              :select-attribute="selectattr"
              :theme-styles="themeStyles"
              tint-color="#FFE259"
              show-caps
            ></v-date-picker>
          </div>
          <div v-else>
            <template v-if="showwhite">
              <a
                @click="goback('formulario') "
                style="cursor: pointer"
              >{{multilanguage.nocalendar[language]}}</a>
            </template>
            <template v-else>
              <a
                @click="goback('formulario')"
                style="cursor: pointer"
              >{{multilanguage.nocalendar[language]}}</a>
            </template>
          </div>
        </div>
      </div>
    </div>
    <div>
      <template v-if="loader">
        <div>
          <div class="calendar-title">{{multilanguage.gettingrooms[language]}}</div>
          <div class="progress">
            <div
              class="progress-bar progress-bar-striped progress-bar-animated"
              role="progressbar"
              style="width: 100%"
            ></div>
          </div>
        </div>
      </template>
      <template v-else>
        <template v-if="rates" class="col-12">
          <div class="row">
            <div class="col-12 mt-5">
              <div class="calendar-title">{{multilanguage.chooseroom[language]}}</div>
              <div id="accordion">
                <div
                  class="card"
                  v-for="(ocupacion,ocupacionindex) in rates.rates"
                  :key="'f'+ocupacionindex"
                >
                  <div
                    class="card-header"
                    :id="'heading' + ocupacionindex"
                    data-toggle="collapse"
                    :data-target="'#collapse' + ocupacionindex"
                    aria-expanded="true"
                    aria-controls="collapseOne"
                    v-bind:class="{'selected-header' : ocupacion.seleccionado, 'not-selected-header':!ocupacion.seleccionado}"
                  >
                    <h5 class="mb-0">
                      <div class="row">
                        <template v-if="ocupacion.ages">
                          <div class="col 6 align-middle">
                            <span class="align-middle">{{multilanguage.only[language]}}  {{ocupacionindex + 1}}:</span>
                            <i
                              v-for="(n,index1) in parseInt(ocupacion.paxPerRoom ) - parseInt(ocupacion.ages.length)"
                              class="fas fa-male fa-2x align-middle lg-icon"
                              :key="'g'+index1"
                            ></i>
                            <i
                              v-for="(x,index2) in parseInt(ocupacion.ages.length)"
                              class="fas fa-child text-primary fa-1x align-bottom md-icon"
                              :key="'h'+index2"
                            ></i>
                          </div>
                        </template>
                        <template v-else>
                          <div class="col 6 align-middle">
                            <span class="align-middle">{{multilanguage.only[language]}} {{ocupacionindex + 1}}:</span>
                            <i
                              v-for="(n,index3) in parseInt(ocupacion.paxPerRoom )"
                              class="fas fa-male fa-2x align-middle lg-icon"
                              :key="'i'+index3"
                            ></i>
                          </div>
                        </template>
                        <div
                          class="col 6 text-right responsivesubtitle align-middle text-success"
                          style="width:100%;"
                          v-if="ocupacion.seleccionado"
                        >
                          {{ocupacion.roomseleccionado}}
                          {{ocupacion.boardseleccionado}}
                          <br />
                          <span class="responsivetitle">
                            {{ocupacion.priceselected}}
                            {{ocupacion.selectedcurrency}}
                          </span>
                        </div>
                        <div class="col 6 text-right align-middle" v-else>
                          <span class="align-middle text-danger">{{multilanguage.notselected[language]}}</span>
                        </div>
                      </div>
                    </h5>
                  </div>
                  <div
                    :id="'collapse' + ocupacionindex"
                    class="collapse"
                    :class="{'show' : activeroom === ocupacionindex }"
                    :aria-labelledby="'heading' +ocupacionindex"
                    data-parent="#accordion"
                  >
                    <div class="card-body">
                      <template v-if="ocupacion.options.length > 0">
                        <div
                          class="row"
                          v-for="(aloc,indexaloc) in ocupacion.options"
                          style="border: 1px solid lightgrey; padding:5px 0px 10px 0px;"
                          :key="'j'+indexaloc"
                        >
                          <div class="col-sm-12 col-md-4 col-lg-4" style="margin:10px 0px;">
                            <template v-if="aloc.image">
                              <img
                                class="card-img-top"
                                :src="aloc.image"
                                :alt="aloc.roomDescription"
                              />
                            </template>
                            <template v-else>
                              <img class="card-img-top" :src="noimage" :alt="aloc.roomDescription" />
                            </template>
                          </div>
                          <div class="col-sm-12 col-md-8 col-lg-4" style="margin:10px 0px;">
                            <h5 class="card-title">{{ aloc.roomName }}</h5>
                            <div v-if="aloc.allotment > 5">({{aloc.allotment}} {{multilanguage.available[language]}})</div>
                            <div v-else-if="aloc.allotment > 0">
                              <span class="text-danger">({{multilanguage.only[language]}} {{aloc.allotment}} {{multilanguage.available[language]}})</span>
                            </div>
                            <p class="card-text mt-1 mb-4">{{aloc.roomDescription}}</p>
                            <!--<div v-if=" aloc.prices[ocupacion.seleccionado].onRequest !== true"><span class="red-text">- {{price.onRequestText}}</span></div>-->
                          </div>
                          <div class="col-sm-12 col-md-12 col-lg-4" style="margin:10px 0px;">
                            <h5 class="card-title">{{multilanguage.chooseboard[language]}}</h5>
                            <div class="list-group">
                              <button
                                @click="ocupacion.seleccionado = price.key;
                                              ocupacion.priceselected =
                                                price.retailPrice.value;
                                              ocupacion.selectedcurrency =
                                                price.retailPrice.currencyIsoCode;
                                              ocupacion.roomseleccionado = aloc.roomName;
                                              ocupacion.boardseleccionado = price.boardBasisName
                                              calculateTotals();
                                              openNext(ocupacionindex);
                                              isAllSelected();"
                                v-bind:class="{active : ocupacion.seleccionado === price.key}"
                                v-for="(price,indexroom) in aloc.prices"
                                class="list-group-item list-group-item-action"
                                style="cursor: pointer"
                                :key="'k'+indexroom"
                              >
                                <div class="row">
                                  <div class="col text-left align-middle">{{ price.boardBasisName }}</div>
                                  <template v-if="price.offer !== true">
                                    <div
                                      class="col text-right align-middle"
                                    >{{ price.retailPrice.value }} {{ price.retailPrice.currencyIsoCode }}</div>
                                  </template>
                                  <template v-else>
                                    <div class="col text-right">
                                      <i
                                        class="tiny material-icons mini-icon align-middle"
                                        data-toggle="tooltip"
                                        data-placement="bottom"
                                        :title="price.offerText"
                                        style="cursor: pointer;padding-right: 5px;"
                                      >new_releases</i>
                                      <span
                                        class="align-middle"
                                      >{{ price.retailPrice.value }} {{price.retailPrice.currencyIsoCode}}</span>
                                      <!--<i v-if="ocupacion.seleccionado === price.key" class="small material-icons" >check_box</i>-->
                                    </div>
                                  </template>
                                </div>
                              </button>
                            </div>
                          </div>
                        </div>
                      </template>
                      <template v-else style="width: 100%">{{multilanguage.norooms[language]}}</template>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="row text-right mt-4">
            <div class="col-12">
              <h3 class="text-primary">Total: {{Math.round(totalprice * 100) / 100}} {{currency}}</h3>
            </div>
            <div class="col-12 mb-5 button-container">
              <button
                class="btn btn-primary"
                v-bind:class="{disabled: !allrooms  }"
                v-on:click="abrirdetalle"
              >{{multilanguage.goon[language]}}</button>
            </div>
          </div>
        </template>
        <template v-else>
          <h5 class="center">{{multilanguage.norates[language]}}</h5>
        </template>
      </template>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import helper from "@/script/helper";
import Ficha from "@/components/commons/Ficha";

var globallanguage = "es";
if (globaldata.language != null && globaldata.language != "") {
  globallanguage = globaldata.language;
}
var iswhitebrand = false;
if (globaldata.whitebrandhotel !== null && globaldata.whitebrandhotel !== "") {
  iswhitebrand = true;
}
const langfile = require("../../assets/lang/content.json");

export default {
  name: "Rates",
  data: function() {
    return {
      activeroom: 0,
      allrooms: false,
      rates: null,
      showwhite: iswhitebrand,
      title: this.$route.query.hotelname,
      pageloaded: false,
      totalprice: null,
      stars: this.$route.query.stars,
      keys: this.$route.query.keys,
      currency: null,
      entrada: this.$route.query.entrada,
      salida: this.$route.query.salida,
      loader: null,
      loaderdate: null,
      hotelkey: this.$route.query.hotelkey,
      hotelid: this.$route.query.hotelid,
      language: globallanguage,
      multilanguage: langfile.multilanguage,
      charged: null,
      //category: this.$route.query.category,
      address: this.$route.query.address,
      disponibilidad: null,
      selectedValue: {
        start: this.$route.query.entrada,
        end: this.$route.query.salida
      },
      disableddates: null,
      availabledates: [],
      localizador: globaldata.localizador,
      formats: {
        title: "MMMM YYYY",
        weekdays: "W",
        navMonths: "MMM",
        input: ["L", "YYYY-MM-DD", "YYYY/MM/DD"], // Only for `v-date-picker`
        dayPopover: "L", // Only for `v-date-picker`
        data: ["L", "YYYY-MM-DD", "YYYY/MM/DD"] // For attribute dates
      },
      themeStyles: {
        wrapper: {
          background: "#fff",
          border: "solid rgba(0, 0, 0, 0.2) 1px",
          borderRadius: "5px",
          padding: "10px"
        },
        headerTitle: {
          padding: "15px 0px",
          fontSize: "26px"
        },
        weekdays: {
          fontSize: "17px"
        },
        weeks: {},
        dayCellNotInMonth: {
          opacity: 0.7
        },
        dayCell: {
          background: "#00C064",
          color: "#fafafa",
          padding: "0px",
          margin: "0px"
        },
        dayContent: {
          margin: "0px",
          fontSize: "17px"
        }
      },
      disableattr: {
        key: "disabled",
        highlight: {
          backgroundColor: "#F84744",
          opacity: 1,
          animated: false
        },
        contentStyle: {
          color: "white"
        }
      },
      enableattr: [
        {
          key: "available",
          highlight: {
            backgroundColor: "green",
            opacity: 1,
            animated: false
          },
          contentStyle: {
            color: "white"
          }
        }
      ],
      dragattr: {
        key: "drag",
        highlight: {
          backgroundColor: "#FFCA3B",
          opacity: 1,
          animated: false
        },
        contentStyle: {
          color: "black"
        }
      },
      selectattr: {
        key: "select",
        highlight: {
          backgroundColor: "#FFE259",
          opacity: 1,
          animated: false
        },
        contentStyle: {
          color: "black"
        }
      },
      from_page: null,
      to_page: null
    };
  },
  components: {
    Ficha: Ficha
  },
  watch: {
    selectedValue: function(val) {
      this.refreshdates();
    }
  },
  methods: {
    openNext(indexroom) {
      let current = "#collapse" + indexroom;
      let next = "#collapse" + (indexroom + 1);
      $(next).collapse("show");
      $(current).collapse("hide");
    },
    isAllSelected: function() {
      var d = this.$data;

      for (var i = 0; i < d.rates.rates.length; i++) {
        if (!d.rates.rates[i].seleccionado) {
          d.allrooms = false;
          return;
        }
      }
      d.allrooms = true;
      return;
    },
    calculateTotals: function() {
      var d = this.$data;
      d.totalprice = 0;
      for (var i = 0; i < d.rates.rates.length; i++) {
        if (d.rates.rates[i].priceselected) {
          d.totalprice = d.totalprice + d.rates.rates[i].priceselected;
        }

        d.currency = d.rates.rates[i].selectedcurrency;
      }
    },
    formatDate: function(date) {
      var d = new Date(date);
      var month = "" + (d.getMonth() + 1);
      var day = "" + d.getDate();
      var year = d.getFullYear();

      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;

      return [year, month, day].join("-");
    },
    getrates(entrada, salida) {
      var d = this.$data;
      var _this = this;
      d.loader = true;

      var bodyFormData = {
        hotelId: d.hotelid,
        //  hotelKey: d.hotelkey,  Prueba añadir hotelkey para hotel beds en GetHotelRatesRQ
        checkin: entrada,
        checkout: salida,
        occupancies: this.$route.query.ocupacion,
        language: globallanguage
      };

      axios
        .post(globaldata.baseurl + "/hotel/rates", bodyFormData, {
          headers: {
            "Content-Type": "application/json"
          }
        })
        .then(function(response) {
          d.loader = null;

          d.totalprice = 0;
          var exitsrates = null;
          for (var z in response.data.rates) {
            if (response.data.rates[z].options[0]) {
              if (response.data.rates[z].options[0].prices[0]) {
                exitsrates = true;
              }
            }
          }
          if (exitsrates) {
            d.rates = response.data;
          } else {
            d.rates = null;
          }
        })
        .catch(function(error) {
          d.loader = null;
          console.log(error);
        });
    },
    refreshdates() {
      var d = this.$data;
      d.entrada = this.formatDate(d.selectedValue.start);
      d.salida = this.formatDate(d.selectedValue.end);
      this.getrates(
        d.entrada ? d.entrada.replace(/-/g, "") : null,
        d.salida ? d.salida.replace(/-/g, "") : null
      );
    },
    goback(page) {
      switch (page) {
        case "formulario":
          this.$router.push({
            name: "main",
            query: {
              destino: this.$route.query.destino,
              iddestino: this.$route.query.iddestino,
              salida: this.$route.query.salida,
              entrada: this.$route.query.entrada,
              ocupacion: this.$route.query.ocupacion
            }
          });
          break;
        case "dispo":
          this.$router.push({
            name: "hotel_dispo",
            query: {
              destino: this.$route.query.destino,
              iddestino: this.$route.query.iddestino,
              salida: this.$data.salida,
              entrada: this.$data.entrada,
              ocupacion: this.$route.query.ocupacion
            }
          });
          break;
        case "rates":
          this.$router.push({
            name: "hotel_rates",
            query: {
              hotelkey: this.$route.query.hotelkey,
              hotelid: this.$route.query.hotelid,
              hotelname: this.$route.query.hotelname,
              stars: this.$route.query.stars,
              keys: this.$route.query.keys,
              address: this.$route.query.address,
              salida: this.$data.salida,
              entrada: this.$data.entrada,
              ocupacion: this.$route.query.ocupacion
            }
          });
          break;
        default:
      }
    },
    abrirdetalle: function(e) {
      if (this.$data.allrooms) {
        var s;
        for (var z in this.$data.rates.rates) {
          if (s) s += ",";
          else s = "";
          s += this.$data.rates.rates[z].seleccionado;

          for (var x in this.$data.rates.rates[z].options) {
            if (x.key === this.$data.rates.rates[z].seleccionado) {
            }
          }
        }
        this.$router.push({
          name: "hotel_detalle",
          query: {
            ratekeys: s,
            hotelkey: this.$route.query.hotelkey,
            hotelid: this.$route.query.hotelid,
            hotelname: this.$route.query.hotelname,
            category:  this.$route.query.category,
            stars: this.$route.query.stars,
            keys: this.$route.query.keys,
            address: this.$route.query.address,
            salida: this.$data.salida,
            entrada: this.$data.entrada,
            ocupacion: this.$route.query.ocupacion,
            iddestino: this.$route.query.iddestino,
            destino: this.$route.query.destino
          }
        });
      }
    },
    getNameCat() {
      var d = this.$data;
      var _this = this;
      axios({
        method: "get",
        url: globaldata.baseurl + "/hotel/available",
        params: {
          language: globallanguage,
          resorts: globaldata.whitebrandhotel,
          checkin: this.$route.query.entrada
            ? this.$route.query.entrada.replace(/-/g, "")
            : null,
          checkout: this.$route.query.salida
            ? this.$route.query.salida.replace(/-/g, "")
            : null,
          occupancies: this.$route.query.ocupacion
        }
      })
        .then(function(response) {
          _this.$route.query.hotelname = response.data.hotels[0].hotelName;
          _this.$route.query.stars =
            response.data.hotels[0].stars;
          d.stars = response.data.hotels[0].stars;
           _this.$route.query.keys =
            response.data.hotels[0].keys;
          d.keys = response.data.hotels[0].keys;
          _this.$route.query.address = response.data.hotels[0].address;
          d.address = response.data.hotels[0].address;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getavailabledates() {
      var d = this.$data;
      var _this = this;
      d.loaderdate = true;
      var fpage = {
        month: parseInt(this.$route.query.entrada.substr(5, 2)),
        year: this.$route.query.entrada.substr(0, 4)
      };
      var tpage = {
        month: parseInt(this.$route.query.entrada.substr(5, 2)) + 1,
        year: this.$route.query.entrada.substr(0, 4)
      };

      d.to_page = tpage;
      d.from_page = fpage;

      axios({
        method: "get",
        url: globaldata.baseurl + "/cms/hotelavailabilitycalendar",
        params: {
          resorts: this.$route.query.hotelid,
          checkin: this.$route.query.entrada
            ? this.$route.query.entrada.replace(/-/g, "")
            : null,
          checkout: this.$route.query.salida
            ? this.$route.query.salida.replace(/-/g, "")
            : null,
          occupancies: this.$route.query.ocupacion
        }
      })
        .then(function(response) {
          var isFirst = true;
          var startdate;
          var enddate;
          var lastdate;
          var lastdateTrans;

          d.loaderdate = null;

          d.disponibilidad = response.data;
          if (d.disponibilidad) {
            for (var i = 0; i < d.disponibilidad.months.length; i++) {
              for (
                var j = 0;
                j < d.disponibilidad.months[i].weeks.length;
                j++
              ) {
                for (
                  var k = 0;
                  k < d.disponibilidad.months[i].weeks[j].days.length;
                  k++
                ) {
                  if (
                    d.disponibilidad.months[i].weeks[j].days[k].date !== null &&
                    d.disponibilidad.months[i].weeks[j].days[k].styleName ===
                      "av"
                  ) {
                    if (isFirst) {
                      startdate =
                        d.disponibilidad.months[i].weeks[j].days[k].date;
                      enddate = null;
                      isFirst = false;
                    } else {
                      var actualDate = new Date(
                        d.disponibilidad.months[i].weeks[j].days[k].date
                      );
                      actualDate.setDate(actualDate.getDate() - 1);
                      actualDate.setMilliseconds(0);
                      actualDate.setSeconds(0);
                      actualDate.setMinutes(0);
                      actualDate.setHours(0);
                      if (actualDate.getTime() !== lastdateTrans.getTime()) {
                        var ldate = lastdateTrans;
                        ldate.setDate(ldate.getDate() + 1);
                        enddate = _this.formatDate(ldate);
                        d.availabledates.push({
                          start: startdate,
                          end: enddate
                        });
                        startdate =
                          d.disponibilidad.months[i].weeks[j].days[k].date;
                      } else {
                      }
                    }
                    lastdate = d.disponibilidad.months[i].weeks[j].days[k].date;
                    lastdateTrans = new Date(
                      d.disponibilidad.months[i].weeks[j].days[k].date
                    );

                    lastdateTrans.setMilliseconds(0);
                    lastdateTrans.setSeconds(0);
                    lastdateTrans.setMinutes(0);
                    lastdateTrans.setHours(0);
                  }
                }
              }
            }
            var ldate = new Date(lastdate);
            ldate.setDate(ldate.getDate() + 1);
            enddate = _this.formatDate(ldate);
            console.log("este es el ultimo " + startdate + enddate);
            if (startdate && enddate) {
              d.availabledates.push({ start: startdate, end: enddate });
            }
          }
          console.log(d.availabledates);
          if (d.availabledates.length === 0) {
            d.availabledates = null;
            d.disableddates = { start: null, end: null };
          }
          console.log(d.availabledates);
          d.charged = true;
        })
        .catch(function(error) {
          d.charged = false;
          d.loaderdate = null;
          console.log(error);
        });
    }
  },
  mounted() {
    var d = this.$data;

    if (d.showwhite) {
      this.getNameCat();
    }
    this.getrates(d.entrada.replace(/-/g, ""), d.salida.replace(/-/g, ""));
    this.getavailabledates();
    helper.scrollToId("main-app");
  }
};
